@import url(https://fonts.googleapis.com/css2?family=Abril+Fatface&family=Merriweather:ital@0;1&family=Poppins:wght@100;300;400;500&family=Quicksand:wght@700&family=Roboto:wght@300&family=Space+Mono&family=Work+Sans:wght@400;500;800&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Abril+Fatface&family=Merriweather:ital@0;1&family=Poppins:wght@100;300;400;500&family=Quicksand:wght@700&family=Roboto:wght@300&family=Space+Mono&family=Work+Sans:wght@400;500;800&display=swap);
*{
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: 'Poppins', sans-serif;;
}

body {
  background: black;
}

h1, h4, p, a{
  color: white;
  text-decoration: none;
}

ul{
  list-style-type: none;
}

.btn {
  padding: 12px 32px;
  font-size: 1rem;
  text-transform: uppercase;
  background: rgb(red, green, blue);
  color: #222;
  border: 1px solid #fff;
  font-weight: 600;
  cursor: pointer;
}

.btn-light {
  background: transparent;
  color: #fff;
}

.btn:hover{
  background: rgba(red, green, blue, alpha);
  color: #fff;
  transition: .3s;
}
.header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 1rem;
    position: fixed;
    width: 100%;
    height: 90px;
    z-index: 10;
}

.header-bg {
    background-color: rgb(0, 0 , 0, .85);
    transition: .5s;
    
}
.nav-menu {
    display: flex;
}

.nav-menu li{
    padding: 0 1rem;
}

.nav-menu li a{
    font-size: 1.2rem;
    font-weight: 200;
}

.hamburger{
    display: none;
}

@media screen and (max-width: 1040px) {
    .nav-menu{
        flex-direction: column;
        justify-content: center;
        align-items: center;
        width: 100%;
        height: 100vh;
        background: rgba(0, 0, 0, .9);
        position: absolute;
        top: 0;
        left: -100%;
        z-index: -3;
        transition: .3s;
    }   
    
.nav-menu.active {
    left: 0;
}


.nav-menu li {
    padding: 1rem 0;
}

.nav-menu li a {
    font-size: 2rem;
}

.hamburger{
    display: initial;
}
}
.footer {
    width: 100%;
    padding: 1rem 0;
    background-color: rgb(19, 19, 19, 0.8);
    margin-top: 10px;
}

.footer .container {
    max-width: 1140px;
    margin: auto;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-gap: 40px;
}

.left {
    height: 100%;
    max-width: 100%;
    display: flex;
    flex-direction: column;
    padding: 1rem;
}

.location {
    display: flex;
    margin-bottom: .8rem;
}

.location p{
    line-height: 30px;
}

.right{
    height: 100%;
    max-width: 100%;
    display: flex;
    flex-direction: column;
    padding: 1rem;
}

.social{
    margin-top: 1rem;
}

h4{
    font-size: 1.2rem;
    padding-bottom: .7rem;
}

p{
    font-size: 1.2rem;
}

@media screen and (max-width: 640px) {
    .footer .container {
        grid-template-columns: 1fr;
      
    }
    
}
.hero-img {
    width: 100%;
    background: rgba(0, 0,0 0.5);
    height: 60vh;
    position: relative;
}

.heading{
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.hero-img::before{
    content: "";
    background: "../assets/IMG_6956.JPG)";
    background-size: cover;
    background-position: center top;
    height: 100%;
    width: 100%;
    position: absolute;
    top: 0px;
    left: 0px;
    z-index: -1;
}

.hero-img h1{
    font-size: 2.4rem;
}

.hero-img p{
    font-size: 1.4rem;
    text-align: center;
}

@media screen and (max-width: 640px) {
    .hero-img h1{
        font-size: 2rem;
    }
    
    .hero-img p{
        font-size: 1.4rem;
        text-align: center;
    }
    
}
*{
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: 'Poppins', sans-serif;
  color: white;
}

.resume {
    font-size: 20px;
}

.resume a {
    color: lightpink;
    padding: 4px;
  }
.about {
    width: 100%;
    margin: 3rem 0;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-wrap: wrap;
    height: 100%;
}

.about .left{
    text-align: center;
    margin: auto;
    padding: 1rem;
    max-width: 350px;
}

.about .left p {
    margin: 1.2rem 0;

}

.about .right {
    max-width: 700px;

}

.right .img-container {
    display: grid;
    grid-template-columns: repeat(12, 1fr);
    position: relative;
    align-items: center;
    text-align: center;

}

.right .img {
    max-width: 75%;
    border: 1px solid #333;
}

.right .top{
    grid-column: 1 /span 8;
    grid-row: 1;
    padding-top: 20%;
    z-index: 6;
}

.right .bottom {
    grid-column: 4 /-1;
    grid-row: 1;
    z-index: 5;
}
.work-container {
    position: relative;
    width: calc(33.33% - 10px);
    height: 250px;
    margin: 50px 0 0;
    object-fit: cover;
    border: 3px solid black;
    box-sizing: border-box;
    padding: 5px;
    float: left;
  }
  
  .work-container:nth-child(3n+1) {
    clear: left;
  }
  
  .work-container h3 {
    padding: 5px;
    position: absolute;
    bottom: 3;
    line-height: 1.2;
    width: 100%;
    text-align: center;
    color: white;
    z-index: 1; /* added z-index */
  }
  
  .work-container img {
    -webkit-filter: brightness(80%);
            filter: brightness(80%);
    border: 2px solid white;
    width: 100%;
    height: 100%;
    object-fit: cover;
    object-position: center;
  }
  
  .work-container img:hover {
    -webkit-filter: brightness(100%);
            filter: brightness(100%);
  }
  
  .work-container p {
    font-size: 20px;
    padding: 5px;
    position: absolute;
    bottom: 0%;
    line-height: 2px;
  }
  
  .work-container p a {
    color: white;
  }
  
  .work-container p a:hover {
    color: black;
  }

  .work-container:nth-last-child(-n+3) {
    margin-bottom: 5px;
  }

  h3{
    color: white;
  }


label {
    color: white;
    margin-bottom: .5rem;
}

form{
    display: flex;
    flex-direction: column;
    padding: 4rem 1rem;
    margin: auto;
    max-width: 600px;
}

form input, form textarea{
    margin-bottom: 1rem;
    padding: 10px 18px;
    font-size: 1.2rem;
    background-color:rgba(255, 255, 255, .1);
    border-color: rgba(255, 255, 255, .1);
    color: #f4f4f4;
}
.mask {
    width: 100%;
    height: 100vh;
    position: relative;
}

.btn{
    background-color: lightpink;
}

.home-img{
    width: 100%;
    height: 100vh;
    background-size: cover;
    background-position: center;
    object-fit: cover;
}

.mask::after {
    content: "";
    width: 100%;
    height: 100%;
    position: absolute;
    top:0;
    left:0;
    background-color: black;
    opacity: .7;
}

.hero {
    height: 100%;
    width: 100%
}

.hero .content {
    position: absolute;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    top: 50%;
    left: 50%;
    text-align: center;
}
.hero .content h1 {
    font-size: 4rem;
    padding: .6rem 0 1.5rem;
}

.hero .content p{
    font-size: 1.4rem;
    font-weight: 200;
    text-transform: uppercase;
}

@media screen and (max-width: 640px) {
    .hero .content h1 {
        font-size: 3rem;
    }
    
    .hero .content p{
        font-size: 1.4rem;
    }
}
